export class BsArrayTypeComponent extends FieldArrayType {
  constructor(builder: FormlyFormBuilder) {
    super(builder);
  }

  move(i, step) {
    this.model.splice(i + step, 0, ...this.model.splice(i, 1));
    (<any> this.options)._buildForm();
  }
}
